:root {
  --blue: #0e0620;
  --white: #fff;
  --green: #2ccf6d;
}

.btn {
  z-index: 1;
  overflow: hidden;
  background: transparent;
  position: relative;
  padding: 8px 50px;
  border-radius: 30px;
  cursor: pointer;
  font-size: 1em;
  letter-spacing: 2px;
  transition: 0.2s ease;
  font-weight: bold;
  margin: 5px 0px;
  width: 15rem;
  padding: 14px;
}
.btn.green {
  border: 2px solid #2ccf6d;
  border: 2px solid var(--green);
  color: #0e0620;
  color: var(--blue);
}
.btn.green:before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 0%;
  height: 100%;
  background: #2ccf6d;
  background: var(--green);
  z-index: -1;
  transition: 0.2s ease;
}

.notfoundtext{
  margin: 10rem 15px 0px 15px;
  font-family: cursive;
  color: #8c8c8c;
}

.btn.green:hover {
  color: #fff;
  color: var(--white);
  background: #2ccf6d;
  background: var(--green);
  transition: 0.2s ease;
}
.btn.green:hover:before {
  width: 100%;
}

.align-self-center
{
    display: grid;
}

@media only screen and (max-width: 768px) {
  .notfoundtext{
    margin: 1rem 15px 0px 15px;
  }
}

